	module.exports = {"data":{"markdownRemark":{"html":"<h2>Area of a Circle</h2>\n<p>The area of a circle is all the space inside a circle’s circumference. </p>\n<p>If ‘r’ is the radius of circle, its area is calculated with formula pi*r<sup>2</sup> where pi is mathematical constant.</p>\n<p>pi = 22/7 = 3.141592…</p>\n<p>A = pi *r<sup>2</sup>, r is the radius of circle</p>\n<p>A = pi * {d<sup>2</sup>/4}, d is the diameter of circle, d=2* r</p>\n<p><img src=\"http://areacircle.com/Area_of_a_circle_basics.png\" alt=\"alt\"></p>\n<p>Area of a circle is defined as the space enclosed by the circumference of the circle.</p>\n<h4>Terminology</h4>\n<p>Circumference (C) - The enclosing boundary of the circle is called circumference of the circle.</p>\n<p>Radius (r) - The length of a line from any point on the boundary/cirumference of the circle to the centre of the circle is known as the radius of the circle.</p>\n<p>Diameter (d) - The length of the line that passes across the circle through the centre of the circle, is called the diameter.</p>\n<p>Pi (<span class=\"texhtml\">π</span>) - A mathematical constant which is approximated as 3.14</p>\n<h4>Formula</h4>\n<h6>Given radius of the circle</h6>\n<p>Area of circle =  <span class=\"texhtml\"><span class=\"texhtml\">π</span> * <i>radius</i><sup>2</sup></span></p>\n<h6>Given diameter of the circle</h6>\n<p>We know that <code>diameter = 2 * radius</code>, so you can derive the area of the circle as below,</p>\n<p>Area of circle =  <span class=\"texhtml\"><span class=\"texhtml\">π</span> * <i>(diameter/2)</i><sup>2</sup></span></p>\n<h6>Given circumference of the circle</h6>\n<p>We know that the circumference of the circle (C),\n<br><code>C = 2 * π * (radius)</code>.\n<br><br>From this, you can derive the area of the circle as below,</p>\n<p>Area of circle =  <i>C</i><sup>2</sup></span> / 4 * <span class=\"texhtml\"><span class=\"texhtml\">π</span></p>\n<h4>Examples</h4>\n<ol>\n<li>\n<p>Given radius = 3cm, find area of the circle</p>\n<p> Area = 3.14 * 9 = 28.26 <span>cm<sup>2</sup></span></p>\n</li>\n<li>\n<p>Given diameter = 8cm, find area of the circle</p>\n<p> Radius = (Diameter/2) = 4cm</p>\n<p> Area = 3.14 * 16 = 50.24 <span>cm<sup>2</sup></span></p>\n</li>\n<li>\n<p>Given circumference of a circle = 25cm, find the area of the circle</p>\n<p> Area = 625 / (4 * 3.14) = 49.76 <span>cm<sup>2</sup></span></p>\n</li>\n</ol>\n<p>A “Real World” Example:-</p>\n<p>Example: Max is building a house. The first step is to drill holes and fill them with concrete.\nThe holes are 0.4 m wide and 1 m deep, how much concrete should Max order for each hole?</p>\n<p>The holes are circular (in cross section) because they are drilled out using an auger.\nThe diameter is 0.4m, so the Area is:\nA = (π/4) × D2\nA = (3.14159…/4) × 0.42\nA = 0.7854… × 0.16\nA = 0.126 m2 (to 3 decimals)\nAnd the holes are 1 m deep, so:\nVolume = 0.126 m2 × 1 m = 0.126 m3\nSo Max should order 0.126 cubic meters of concrete to fill each hole.</p>\n<p>Note: Max could have estimated the area by:</p>\n<ol>\n<li>Calculating a square hole: 0.4 × 0.4 = 0.16 m2</li>\n<li>Taking 80% of that (estimates a circle): 80% × 0.16 m2 = 0.128 m2</li>\n<li>And the volume of a 1 m deep hole is: 0.128 m3</li>\n</ol>\n<h4>More Information</h4>\n<p>More information with illustrations can be found <a href=\"https://www.wikihow.com/Calculate-the-Area-of-a-Circle\">WikiHow</a></p>","fields":{"slug":"/mathematics/area-of-a-circle/"},"frontmatter":{"title":"Area of a Circle"}}},"pathContext":{"slug":"/mathematics/area-of-a-circle/"}}

